import { useEffect, useState } from "react"
import { toast } from "react-hot-toast"
import Image from "next/image"
import { Trash2Icon, EditIcon, XIcon } from "lucide-react"

export default function StoreManageProducts() {
    const currency = process.env.NEXT_PUBLIC_CURRENCY_SYMBOL || '$'
    const categories = ['Fresh', 'Upvas', 'special']

    const [loading, setLoading] = useState(true)
    const [products, setProducts] = useState([])
    const [editingProduct, setEditingProduct] = useState(null)
    const [isEditModalOpen, setIsEditModalOpen] = useState(false)

    const [editForm, setEditForm] = useState({
        productId: '',
        name: '',
        description: '',
        mrp: 0,
        price: 0,
        category: '',
        isLatest: false,
        isBestSelling: false,
        existingImages: [],
        newImages: []
    })

    const fetchProducts = async () => {
        try {
            const token = await getToken()
            const { data } = await axios.get('/api/store/product', {
                headers: { Authorization: `Bearer ${token}` }
            })
            setProducts(data.products)
        } catch (error) {
            toast.error(error?.response?.data?.error || error.message)
        }
        setLoading(false)
    }

    const toggleStock = async (productId) => {
        try {
            const token = await getToken()
            const { data } = await axios.post('/api/store/stock-toggle', { productId }, {
                headers: { Authorization: `Bearer ${token}` }
            })
            setProducts(prevProducts => prevProducts.map(product =>
                product.id === productId ? { ...product, inStock: !product.inStock } : product
            ))
            toast.success(data.message)
        } catch (error) {
            toast.error(error?.response?.data?.error || error.message)
        }
    }

    const deleteProduct = async (productId) => {
        if (!window.confirm('Are you sure you want to delete this product?')) return

        try {
            const token = await getToken()
            const { data } = await axios.delete(`/api/store/product?productId=${productId}`, {
                headers: { Authorization: `Bearer ${token}` }
            })
            setProducts(prevProducts => prevProducts.filter(p => p.id !== productId))
            toast.success(data.message)
        } catch (error) {
            toast.error(error?.response?.data?.error || error.message)
        }
    }

    const openEditModal = (product) => {
        setEditingProduct(product)
        setEditForm({
            productId: product.id,
            name: product.name,
            description: product.description,
            mrp: product.mrp,
            price: product.price,
            category: product.category,
            isLatest: product.isLatest || false,
            isBestSelling: product.isBestSelling || false,
            existingImages: product.images,
            newImages: []
        })
        setIsEditModalOpen(true)
    }

    const closeEditModal = () => {
        setIsEditModalOpen(false)
        setEditingProduct(null)
        setEditForm({
            productId: '',
            name: '',
            description: '',
            mrp: 0,
            price: 0,
            category: '',
            isLatest: false,
            isBestSelling: false,
            existingImages: [],
            newImages: []
        })
    }

    const removeExistingImage = (index) => {
        setEditForm(prev => ({
            ...prev,
            existingImages: prev.existingImages.filter((_, i) => i !== index)
        }))
    }

    const handleNewImageUpload = (e) => {
        const files = Array.from(e.target.files)
        setEditForm(prev => ({
            ...prev,
            newImages: [...prev.newImages, ...files]
        }))
    }

    const removeNewImage = (index) => {
        setEditForm(prev => ({
            ...prev,
            newImages: prev.newImages.filter((_, i) => i !== index)
        }))
    }

    const updateProduct = async (e) => {
        e.preventDefault()
        
        try {
            const formData = new FormData()
            formData.append('productId', editForm.productId)
            formData.append('name', editForm.name)
            formData.append('description', editForm.description)
            formData.append('mrp', editForm.mrp)
            formData.append('price', editForm.price)
            formData.append('category', editForm.category)
            formData.append('isLatest', editForm.isLatest)
            formData.append('isBestSelling', editForm.isBestSelling)
            formData.append('existingImages', JSON.stringify(editForm.existingImages))
            
            editForm.newImages.forEach(image => {
                formData.append('newImages', image)
            })

            const token = await getToken()
            const { data } = await axios.put('/api/store/product', formData, {
                headers: { Authorization: `Bearer ${token}` }
            })
            
            toast.success(data.message)
            closeEditModal()
            fetchProducts()
        } catch (error) {
            toast.error(error?.response?.data?.error || error.message)
        }
    }

    useEffect(() => {
        fetchProducts()
    }, [])

    if (loading) return <div className="flex justify-center p-20">Loading...</div>

    return (
        <>
            <h1 className="text-2xl text-slate-500 mb-5">
                Manage <span className="text-slate-800 font-medium">Products</span>
            </h1>
            
            <div className="overflow-x-auto">
                <table className="w-full max-w-5xl text-left ring ring-slate-200 rounded overflow-hidden text-sm">
                    <thead className="bg-slate-50 text-gray-700 uppercase tracking-wider">
                        <tr>
                            <th className="px-4 py-3">Product</th>
                            <th className="px-4 py-3 hidden md:table-cell">Category</th>
                            <th className="px-4 py-3 hidden lg:table-cell">MRP</th>
                            <th className="px-4 py-3">Price</th>
                            <th className="px-4 py-3">Latest</th>
                            <th className="px-4 py-3">Best Sell</th>
                            <th className="px-4 py-3">In Stock</th>
                            <th className="px-4 py-3">Actions</th>
                        </tr>
                    </thead>
                    <tbody className="text-slate-700">
                        {products.map((product) => (
                            <tr key={product.id} className="border-t border-gray-200 hover:bg-gray-50">
                                <td className="px-4 py-3">
                                    <div className="flex gap-2 items-center">
                                        <img src={product.images[0]} alt="" className="w-10 h-10 object-cover rounded" />
                                        <span className="max-w-[150px] truncate">{product.name}</span>
                                    </div>
                                </td>
                                <td className="px-4 py-3 hidden md:table-cell">{product.category}</td>
                                <td className="px-4 py-3 hidden lg:table-cell">{currency}{product.mrp}</td>
                                <td className="px-4 py-3">{currency}{product.price}</td>
                                <td className="px-4 py-3">
                                    <span className={`px-2 py-1 rounded text-xs ${product.isLatest ? 'bg-blue-100 text-blue-700' : 'bg-gray-100 text-gray-500'}`}>
                                        {product.isLatest ? 'Yes' : 'No'}
                                    </span>
                                </td>
                                <td className="px-4 py-3">
                                    <span className={`px-2 py-1 rounded text-xs ${product.isBestSelling ? 'bg-green-100 text-green-700' : 'bg-gray-100 text-gray-500'}`}>
                                        {product.isBestSelling ? 'Yes' : 'No'}
                                    </span>
                                </td>
                                <td className="px-4 py-3 text-center">
                                    <label className="relative inline-flex items-center cursor-pointer">
                                        <input
                                            type="checkbox"
                                            className="sr-only peer"
                                            onChange={() => toggleStock(product.id)}
                                            checked={product.inStock}
                                        />
                                        <div className="w-9 h-5 bg-slate-300 rounded-full peer peer-checked:bg-green-600"></div>
                                        <span className="dot absolute left-1 top-1 w-3 h-3 bg-white rounded-full transition-transform peer-checked:translate-x-4"></span>
                                    </label>
                                </td>
                                <td className="px-4 py-3">
                                    <div className="flex gap-2">
                                        <button
                                            onClick={() => openEditModal(product)}
                                            className="p-2 hover:bg-blue-50 rounded text-blue-600"
                                        >
                                            <EditIcon size={16} />
                                        </button>
                                        <button
                                            onClick={() => deleteProduct(product.id)}
                                            className="p-2 hover:bg-red-50 rounded text-red-600"
                                        >
                                            <Trash2Icon size={16} />
                                        </button>
                                    </div>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>

            {/* Edit Modal */}
            {isEditModalOpen && (
                <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
                    <div className="bg-white rounded-lg w-full max-w-2xl max-h-[90vh] overflow-y-auto p-6">
                        <div className="flex justify-between items-center mb-4">
                            <h2 className="text-xl font-semibold">Edit Product</h2>
                            <button onClick={closeEditModal} className="p-1 hover:bg-gray-100 rounded">
                                <XIcon size={20} />
                            </button>
                        </div>

                        <form onSubmit={updateProduct} className="space-y-4">
                            <div>
                                <label className="block text-sm font-medium mb-1">Product Name</label>
                                <input
                                    type="text"
                                    value={editForm.name}
                                    onChange={(e) => setEditForm({ ...editForm, name: e.target.value })}
                                    className="w-full p-2 border rounded"
                                    required
                                />
                            </div>

                            <div>
                                <label className="block text-sm font-medium mb-1">Description</label>
                                <textarea
                                    value={editForm.description}
                                    onChange={(e) => setEditForm({ ...editForm, description: e.target.value })}
                                    rows={4}
                                    className="w-full p-2 border rounded resize-none"
                                    required
                                />
                            </div>

                            <div className="grid grid-cols-2 gap-4">
                                <div>
                                    <label className="block text-sm font-medium mb-1">MRP ({currency})</label>
                                    <input
                                        type="number"
                                        value={editForm.mrp}
                                        onChange={(e) => setEditForm({ ...editForm, mrp: Number(e.target.value) })}
                                        className="w-full p-2 border rounded"
                                        required
                                    />
                                </div>
                                <div>
                                    <label className="block text-sm font-medium mb-1">Price ({currency})</label>
                                    <input
                                        type="number"
                                        value={editForm.price}
                                        onChange={(e) => setEditForm({ ...editForm, price: Number(e.target.value) })}
                                        className="w-full p-2 border rounded"
                                        required
                                    />
                                </div>
                            </div>

                            <div>
                                <label className="block text-sm font-medium mb-1">Category</label>
                                <select
                                    value={editForm.category}
                                    onChange={(e) => setEditForm({ ...editForm, category: e.target.value })}
                                    className="w-full p-2 border rounded"
                                    required
                                >
                                    {categories.map(cat => (
                                        <option key={cat} value={cat}>{cat}</option>
                                    ))}
                                </select>
                            </div>

                            <div className="flex gap-4">
                                <label className="flex items-center gap-2">
                                    <input
                                        type="checkbox"
                                        checked={editForm.isLatest}
                                        onChange={(e) => setEditForm({ ...editForm, isLatest: e.target.checked })}
                                        className="w-4 h-4"
                                    />
                                    <span>Show in Latest</span>
                                </label>
                                <label className="flex items-center gap-2">
                                    <input
                                        type="checkbox"
                                        checked={editForm.isBestSelling}
                                        onChange={(e) => setEditForm({ ...editForm, isBestSelling: e.target.checked })}
                                        className="w-4 h-4"
                                    />
                                    <span>Show in Best Selling</span>
                                </label>
                            </div>

                            <div>
                                <label className="block text-sm font-medium mb-2">Current Images</label>
                                <div className="flex gap-2 flex-wrap">
                                    {editForm.existingImages.map((img, index) => (
                                        <div key={index} className="relative">
                                            <img src={img} alt="" className="w-20 h-20 object-cover rounded" />
                                            <button
                                                type="button"
                                                onClick={() => removeExistingImage(index)}
                                                className="absolute -top-2 -right-2 bg-red-500 text-white rounded-full p-1"
                                            >
                                                <XIcon size={12} />
                                            </button>
                                        </div>
                                    ))}
                                </div>
                            </div>

                            <div>
                                <label className="block text-sm font-medium mb-2">Add New Images</label>
                                <input
                                    type="file"
                                    accept="image/*"
                                    multiple
                                    onChange={handleNewImageUpload}
                                    className="w-full p-2 border rounded"
                                />
                                <div className="flex gap-2 flex-wrap mt-2">
                                    {editForm.newImages.map((img, index) => (
                                        <div key={index} className="relative">
                                            <img src={URL.createObjectURL(img)} alt="" className="w-20 h-20 object-cover rounded" />
                                            <button
                                                type="button"
                                                onClick={() => removeNewImage(index)}
                                                className="absolute -top-2 -right-2 bg-red-500 text-white rounded-full p-1"
                                            >
                                                <XIcon size={12} />
                                            </button>
                                        </div>
                                    ))}
                                </div>
                            </div>

                            <div className="flex gap-3 justify-end">
                                <button
                                    type="button"
                                    onClick={closeEditModal}
                                    className="px-4 py-2 border rounded hover:bg-gray-50"
                                >
                                    Cancel
                                </button>
                                <button
                                    type="submit"
                                    className="px-4 py-2 bg-slate-800 text-white rounded hover:bg-slate-900"
                                >
                                    Update Product
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            )}
        </>
    )
}

// Mock functions (replace with actual imports)
function getToken() { return Promise.resolve('mock-token') }
const axios = { 
    get: () => Promise.resolve({ data: { products: [] } }),
    post: () => Promise.resolve({ data: { message: 'Success' } }),
    put: () => Promise.resolve({ data: { message: 'Success' } }),
    delete: () => Promise.resolve({ data: { message: 'Success' } })
}